@import url('https://fonts.googleapis.com/css2?family=Lexend+Deca&family=Montserrat:ital,wght@1,900&display=swap');

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    background: rgba(0, 0, 0, 0.5) url(../img/sunseth.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}


h1{
    font-family: 'Montserrat', sans-serif;
    font-size: 4.5rem;
}

h2{
    font-family: 'Montserrat', sans-serif;
    font-size: 3rem;
}

.weather-wrapper{
    width: 100%;
    height: 100vh;
    position: relative;
}

.weather-wrapper:before{
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: -1;
}

.weather-app{
    max-width: 700px;
    height: 100vh;
    margin: auto;
    padding: 0 1rem;
    position: relative;
    top: 5%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.location{
    text-align: center;
}

.location input{
    background-color: rgba(225, 225, 225, 0.5);
    padding: 1rem;
    border-radius: 50px;
    border: none;
    font-family: 'Lexend Deca', sans-serif;
    color: #262626;
    width: 300px;
    font-size: 1.1rem;
    text-transform: capitalize;
}

.temp{
    text-align: center;
}

.location .loc-name{
    margin-top: 100px;
}

.weather-wrapper .description{
    position: relative;
    right: -90%;
    transform-origin:  0 0;
    transform: rotate(270deg);
    font-family: 'Lexend Deca', sans-serif;
    font-size: 1.5rem;
}

.weather-wrapper .bottom{
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    width: 100%;
    margin: 1rem auto;
    padding: 1rem;
    border-radius: 17px;
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff;
    font-family: 'Lexend Deca', sans-serif;
}